<template>
  <div class="lulu-compass">
    <div v-if="step===1" class="compass-1-bg">
      <img class="compass-1-locate" src="@/assets/images/lulu/lulu-small-locate.png" alt="ghost">
      <div class="compass-1-text">怪奇佈下結界來隱藏自己，使我們沒辦法知道他們的精確位置。結界都會有三個端點，只要你盡量靠近「定位點」，正確擺放端點到羅盤上，取得破解結界的密碼，即可進入結界。博物館這邊會計算出三個端點，請速至地圖的「定位點」附近待命。</div>
      <div @click="goTo2" class="compass-1-btn">已抵達</div>
    </div>
    <div v-if="step===2" class="compass-2-bg">
      <img class="compass-2-board" src="@/assets/images/lulu/lulu-center-temple.png" alt="ghost">
      <img class="compass-2-board" src="@/assets/images/lulu/lulu-park-street.png" alt="ghost">
      <img class="compass-2-board" src="@/assets/images/lulu/lulu-old-temple.png" alt="ghost">
      <div class="compass-2-text">博物館已經偵測出結界的三個端點。請站在『定位點』上，調整羅盤直到它正確呈現這三個地點的方位，破解結界的密碼就會顯現，即可進入結界。請拿出道具包的「怪奇羅盤」定位。</div>
      <div class="compass-2-green-text">請拿出道具包的「怪奇羅盤」定位。</div>
      <input v-model="code" class="compass-2-input" type="text">
      <div @click="go" class="compass-2-btn">GO</div>
    </div>
    <div v-if="step===3" class="compass-3-bg">
      <div class="compass-3-text-box">
        <div>委託行街口出現了４尊樣貌獨特的怪物石雕，是怪奇召喚出來護衛自己的守護獸。只有其中１隻是真正的守護獸，其餘是假的分身。如果猜到守護獸的真身，可以消耗３點能力值「識破」牠，逼牠現形。</div>
      </div>
      <div class="compass-3-dot-box">
        <div @click="showFirstAnimal" class="compass-3-dot-1"></div>
        <div @click="showSecondAnimal" class="compass-3-dot-2"></div>
        <div @click="showThirdAnimal" class="compass-3-dot-3"></div>
        <div @click="showFourAnimal" class="compass-3-dot-4"></div>
      </div>
      <!-- 1 -->
      <div @click="closeAllBox" v-if="isShowFirstAnimal" class="compass-3-animal-bg">
        <div class="compass-3-animal-box">
          <img class="compass-3-animal" src="@/assets/images/lulu/eagle-face.png" alt="run-away">
          <div class="compass-3-animal-plus">+</div>
          <img class="compass-3-animal" src="@/assets/images/lulu/whale-tail.png" alt="run-away">
          <div @click="wakeWrong" class="compass-3-animal-wake">喚醒</div>
          <div class="compass-3-animal-text">需消耗3點能力值</div>
        </div>
      </div>
      <!-- 2 -->
      <div @click="closeAllBox" v-if="isShowSecondAnimal" class="compass-3-animal-bg">
        <div class="compass-3-animal-box">
          <img class="compass-3-animal" src="@/assets/images/lulu/owl-face.png" alt="run-away">
          <div class="compass-3-animal-plus">+</div>
          <img class="compass-3-animal" src="@/assets/images/lulu/eel-tale.png" alt="run-away">
          <div @click="wakeWrong" class="compass-3-animal-wake">喚醒</div>
          <div class="compass-3-animal-text">需消耗3點能力值</div>
        </div>
      </div>
      <!-- 3 -->
      <div @click="closeAllBox" v-if="isShowThirdAnimal" class="compass-3-animal-bg">
        <div class="compass-3-animal-box">
          <img class="compass-3-animal" src="@/assets/images/lulu/fence-eagle-face.png" alt="run-away">
          <div class="compass-3-animal-plus">+</div>
          <img class="compass-3-animal" src="@/assets/images/lulu/goldfish-tail.png" alt="run-away">
          <div @click="wakeWrong" class="compass-3-animal-wake">喚醒</div>
          <div class="compass-3-animal-text">需消耗3點能力值</div>
        </div>
      </div>
      <!-- 4 -->
      <div @click="closeAllBox" v-if="isShowFourAnimal" class="compass-3-animal-bg">
        <div class="compass-3-animal-box">
          <img class="compass-3-animal" src="@/assets/images/lulu/tree-eagle-face.png" alt="run-away">
          <div class="compass-3-animal-plus">+</div>
          <img class="compass-3-animal" src="@/assets/images/lulu/fish-tail.png" alt="run-away">
          <div @click="goToHouse" class="compass-3-animal-wake">喚醒</div>
          <div class="compass-3-animal-text">需消耗3點能力值</div>
        </div>
      </div>
      <div @click="closeWrongBox" v-if="isShowWrong" class="compass-3-wrong-bg">
        <div class="compass-3-wrong-box">
          <img style="width: 80vw;" class="compass-3-animal" src="@/assets/images/lulu/fish-bird-black.png" alt="run-away">
          <div class="compass-3-animal-text">這隻只是分身<br>不是真正的守護獸</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'LuluHouseCompass',
  components: {

  },
  data () {
    return {
      step: 1,
      code: '',
      isShowFirstAnimal: false,
      isShowSecondAnimal: false,
      isShowThirdAnimal: false,
      isShowFourAnimal: false,
      isShowWrong: false
    }
  },
  mounted () {

  },
  destroyed () {

  },
  methods: {
    goTo2 () {
      this.step = 2
      this.$store.commit('changeHeader', false)
    },
    go () {
      if (this.code.toLowerCase() === 'xgn') {
        this.step = 3
        alert('答對囉')
      } else {
        alert('答錯囉')
      }
    },
    showFirstAnimal () {
      this.isShowFirstAnimal = true
    },
    showSecondAnimal () {
      this.isShowSecondAnimal = true
    },
    showThirdAnimal () {
      this.isShowThirdAnimal = true
    },
    showFourAnimal () {
      this.isShowFourAnimal = true
    },
    wakeWrong () {
      if (this.power < 3) {
        alert('能力值不足唷！')
        return
      }
      this.$store.commit('changePower', 3)
      // 塞入 local storage
      let vuesList = JSON.parse(localStorage.getItem('vuexList'))
      vuesList.power = this.power
      localStorage.setItem('vuexList', JSON.stringify(vuesList))
      //
      this.closeAllBox()
      this.isShowWrong = true
    },
    closeAllBox () {
      this.isShowFirstAnimal = false
      this.isShowSecondAnimal = false
      this.isShowThirdAnimal = false
      this.isShowFourAnimal = false
    },
    closeWrongBox () {
      this.isShowWrong = false
    },
    goToHouse () {
      if (this.power < 3) {
        alert('能力值不足唷！')
        return
      }
      this.$store.commit('changePower', 3)
      // 塞入 local storage
      let vuesList = JSON.parse(localStorage.getItem('vuexList'))
      vuesList.power = this.power
      localStorage.setItem('vuexList', JSON.stringify(vuesList))
      //
      this.$router.push({
        path: './lulu-house'
      })
    }
  },
  computed: {
    power () {
      return this.$store.state.power
    }
  }
}
</script>
<style lang="scss" scoped>

  .lulu-compass {
    min-height: 100vh;
    padding-top: 100px;
    background: linear-gradient(#000 70%, #95ff00);
    position: relative;
    padding: 0px;

    .compass {

      &-1 {

        &-bg {
          // width: 100vw;
          height: 100vh;
          left: 0;
          top: 0;
          opacity: 0.8;
          display: flex;
          justify-content: center;
          align-items: center;
          flex-direction: column;
          background-image: url('../../assets/images/lulu/lulu-dot-map.png');
          background-size: cover;
          background-position-y: center;
          background-repeat: no-repeat;
          background-position-x: center;

        }

        &-locate {
          width: 45vw;
          height: auto;
        }

        &-text {
          color: white;
          padding: 2px 8px;
          height: 190px;
          overflow: scroll;
        }

        &-btn {
          width: 100%;
          height: 60px;
          line-height: 60px;
          color: white;
          text-align: center;
          background-color: darkgrey;
        }
      }

      &-2 {

        &-bg {
          // width: 100vw;
          height: 100vh;
          left: 0;
          top: 0;
          opacity: 0.8;
          display: flex;
          justify-content: center;
          align-items: center;
          flex-direction: column;
          background-image: url('../../assets/images/lulu/lulu-dot-map.png');
          background-size: cover;
          background-position-y: center;
          background-repeat: no-repeat;
          background-position-x: center;
          padding: 10px;
          margin: auto;
        }

        &-board {
          margin-top: 10px;
          width: 100vw;
          height: auto;
          line-height: 40px;
          text-align: center;
          color: black;

          &:first-child {
            margin-top: 20px;
          }
        }

        &-text {
          color: white;
          padding: 4px 10px;
          font-size: 14px;
        }

        &-green-text {
          color:#95ff00;
          padding: 10px;
        }

        &-input {
            width: 150px;
            padding: 4px;
            color: black;
        }

        &-btn {
          width: 50px;
          height: 50px;
          line-height: 50px;
          text-align: center;
          background: #95ff00;
          color: black;
          margin-top: 10px;
          border-radius: 100%;
        }
      }

      &-3 {

        &-bg {
          height: 100vh;
          left: 0;
          top: 0;
          opacity: 0.8;
          display: flex;
          justify-content: center;
          align-items: center;
          flex-direction: column;
          background-image: url('../../assets/images/lulu/lulu-color-map.png');
          background-size: cover;
          background-position-y: center;
          background-repeat: no-repeat;
          background-position-x: center;
          padding: 10px;
          margin: auto;
        }

        &-text-box {
          min-height: 150px;
          padding: 20px;
          margin-top: 160px;
          color: white;
          z-index: 1;
        }

        &-dot-box {
          position: absolute;
          top: 15vh;
          width: 200px;
          height: 200px;
          background-image: url('../../assets/images/lulu/four-place-map.png');
          background-size: cover;
          background-position-y: center;
          background-repeat: no-repeat;
          background-position-x: center;
        }

        &-dot-1 {
          width: 20px;
          height: 20px;
          background-color: #95ff00;
          border-radius: 50%;
          margin: 50px 0px 0px 103px;
        }

        &-dot-2 {
          width: 20px;
          height: 20px;
          background-color: #95ff00;
          border-radius: 50%;
          margin: -7px 0px 0px 50px;
        }

        &-dot-3 {
          width: 20px;
          height: 20px;
          background-color: #95ff00;
          border-radius: 50%;
          margin: 33px 0px 0px 134px;
        }

        &-dot-4 {
          width: 20px;
          height: 20px;
          background-color: #95ff00;
          border-radius: 50%;
          margin: 1px 0px 0px 57px;
        }

        &-animal-bg {
          color: #95ff00;
          position: fixed;
          width: 100vw;
          height: 100vh;
          background-color: rgba(0, 0, 0, .8);
          display: flex;
          justify-content: center;
          z-index: 1;
        }

        &-animal-box {
          height: max-content;
          padding: 10px;
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          background-color: rgba(0, 0, 0, .8);
          border: 1px solid #95ff00;
          margin-top: 10px;
        }

        &-animal {
        width: 50vw;
        height: auto;
        }

        &-animal-plus {
          font-size: 40px;
          line-height: 30px;
        }

        &-animal-wake {
          border: 1px solid #95ff00;
          border-radius: 50%;
          font-size: 20px;
          padding: 8px;
          margin: 6px 0px;
        }

        &-animal-text {
          font-size: 18px;
        }

        &-wrong-bg {
          color: #95ff00;
          position: fixed;
          width: 100vw;
          height: 100vh;
          background-color: rgba(0, 0, 0, .8);
          display: flex;
          justify-content: center;
          align-items: center;
          z-index: 1;
        }

        &-wrong-box {
          height: max-content;
          padding: 10px;
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          background-color: rgba(0, 0, 0, .8);
          border: 1px solid #95ff00;
        }
      }
    }
  }

</style>



